'use client';

import { useState, useRef } from 'react';
import { Modal, Text, Image, Group, Badge, Tabs, SimpleGrid, ThemeIcon, List, ScrollArea, Box } from '@mantine/core';
import { IconPhoto, IconListDetails, IconTools } from '@tabler/icons-react';
import { Carousel } from '@mantine/carousel';
import Autoplay from 'embla-carousel-autoplay';
import '@mantine/carousel/styles.css';

export function FacilityModal({ facility, onClose }) {
    const [previewImage, setPreviewImage] = useState(null);
    const autoplay = useRef(Autoplay({ delay: 3000 }));

    if (!facility) return null;

    return (
        <>
            <Modal
                opened={!!facility && !previewImage}
                onClose={onClose}
                title={<Text fw={700} size="xl">{facility.name}</Text>}
                size="xl"
                radius="lg"
                padding="xl"
                centered
                scrollAreaComponent={ScrollArea.Autosize}
                zIndex={200}
            >
                <Image
                    src={facility.image}
                    height={300}
                    radius="md"
                    alt={facility.name}
                    fallbackSrc="https://placehold.co/600x400?text=No+Image"
                    mb="xl"
                    style={{ cursor: 'pointer' }}
                    onClick={() => setPreviewImage(facility.image)}
                />

                <Tabs defaultValue="info" color="blue">
                    <Tabs.List mb="md">
                        <Tabs.Tab value="info" leftSection={<IconListDetails size={16} />}>
                            기본 정보
                        </Tabs.Tab>
                        {facility.subItems && (
                            <>
                                <Tabs.Tab value="gallery" leftSection={<IconPhoto size={16} />}>
                                    갤러리
                                </Tabs.Tab>
                                <Tabs.Tab value="specs" leftSection={<IconTools size={16} />}>
                                    상세 제원
                                </Tabs.Tab>
                            </>
                        )}
                    </Tabs.List>

                    <Tabs.Panel value="info">
                        <Text size="lg" mb="md">{facility.description}</Text>

                        <Group gap="xl" mb="xl">
                            <div>
                                <Text size="sm" c="dimmed">위치</Text>
                                <Text fw={500}>{facility.location}</Text>
                            </div>
                            <div>
                                <Text size="sm" c="dimmed">수용 인원</Text>
                                <Text fw={500}>{facility.capacity}</Text>
                            </div>
                        </Group>

                        <Text fw={600} mb="sm">보유 장비</Text>
                        <Group gap="xs">
                            {facility.equipment?.map((item, index) => (
                                <Badge key={index} size="lg" variant="dot">{item}</Badge>
                            ))}
                        </Group>
                    </Tabs.Panel>

                    {facility.subItems && (
                        <>
                            <Tabs.Panel value="gallery">
                                <Carousel
                                    withIndicators
                                    height={400}
                                    slideSize="100%"
                                    slideGap="md"
                                    loop
                                    align="start"
                                    plugins={[autoplay.current]}
                                    onMouseEnter={autoplay.current.stop}
                                    onMouseLeave={autoplay.current.reset}
                                >
                                    {facility.subItems.find(item => item.type === 'image-gallery')?.images.map((img, idx) => (
                                        <Carousel.Slide key={idx}>
                                            <Image
                                                src={img.url}
                                                radius="md"
                                                h={400}
                                                fit="cover"
                                                style={{ cursor: 'pointer' }}
                                                onClick={() => setPreviewImage(img.url)}
                                            />
                                            <Text size="sm" ta="center" mt={4} c="dimmed">{img.caption}</Text>
                                        </Carousel.Slide>
                                    ))}
                                </Carousel>
                                {/* Fallback grid if no gallery subitem but main images exist */}
                                {!facility.subItems.find(item => item.type === 'image-gallery') && facility.images && (
                                    <Carousel
                                        withIndicators
                                        height={400}
                                        slideSize="100%"
                                        slideGap="md"
                                        loop
                                        align="start"
                                        plugins={[autoplay.current]}
                                        onMouseEnter={autoplay.current.stop}
                                        onMouseLeave={autoplay.current.reset}
                                    >
                                        {facility.images.map((img, idx) => (
                                            <Carousel.Slide key={idx}>
                                                <Image
                                                    src={img}
                                                    radius="md"
                                                    h={400}
                                                    fit="cover"
                                                    style={{ cursor: 'pointer' }}
                                                    onClick={() => setPreviewImage(img)}
                                                />
                                            </Carousel.Slide>
                                        ))}
                                    </Carousel>
                                )}
                            </Tabs.Panel>

                            <Tabs.Panel value="specs">
                                {facility.subItems.filter(item => item.type === 'rich-content').map((section, idx) => (
                                    <div key={idx} style={{ marginBottom: '2rem' }}>
                                        <Text fw={700} size="lg" mb="md">{section.title}</Text>
                                        {section.sections.map((subSec, sIdx) => (
                                            <div key={sIdx} style={{ marginBottom: '1rem', padding: '1rem', backgroundColor: 'var(--mantine-color-gray-1)', borderRadius: '8px', borderLeft: `4px solid ${subSec.color || '#ccc'}` }}>
                                                <Text fw={600} mb="xs" c={subSec.color}>{subSec.title}</Text>
                                                <List size="sm" spacing="xs" icon={<ThemeIcon color={subSec.color} size={6} radius="xl" variant="filled" />}>
                                                    {subSec.items.map((item, iIdx) => (
                                                        <List.Item key={iIdx}>{item}</List.Item>
                                                    ))}
                                                </List>
                                            </div>
                                        ))}
                                    </div>
                                ))}
                            </Tabs.Panel>
                        </>
                    )}
                </Tabs>
            </Modal>

            {/* Lightbox Modal */}
            <Modal
                opened={!!previewImage}
                onClose={() => setPreviewImage(null)}
                size="100%"
                fullScreen
                zIndex={300}
                withCloseButton={true}
                styles={{
                    body: { padding: 0, background: 'rgba(0,0,0,0.9)', height: '100vh', display: 'flex', alignItems: 'center', justifyContent: 'center' },
                    header: { position: 'absolute', top: 0, right: 0, zIndex: 301, background: 'transparent' }
                }}
            >
                <Image
                    src={previewImage}
                    fit="contain"
                    h="100%"
                    w="100%"
                    onClick={() => setPreviewImage(null)}
                />
            </Modal>
        </>
    );
}
