'use client';

import React, { useState, useEffect, Suspense, useRef } from 'react';
import { Container, Title, Text, SimpleGrid, Group, TextInput, Button, SegmentedControl, Box, Transition, LoadingOverlay, Paper, Image } from '@mantine/core';
import { Search, Building2 } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import { useSearchParams } from 'next/navigation';
import { Carousel } from '@mantine/carousel';
import Autoplay from 'embla-carousel-autoplay';
import '@mantine/carousel/styles.css';
import { facilities, buildings } from '../../src/data/facilities';
import { FacilityCard } from '../../components/facilities/FacilityCard';
import { FacilityModal } from '../../components/facilities/FacilityModal';

function FacilitiesContent() {
    const [activeBuilding, setActiveBuilding] = useState('main');
    const [selectedFacility, setSelectedFacility] = useState(null);
    const [searchTerm, setSearchTerm] = useState('');
    const searchParams = useSearchParams();
    const autoplay = useRef(Autoplay({ delay: 3000 }));

    useEffect(() => {
        const type = searchParams.get('type');
        if (type) {
            const facility = facilities.find(f => f.id === type);
            if (facility) {
                setActiveBuilding(facility.building);
                setSelectedFacility(facility);
            }
        }
    }, [searchParams]);

    const filteredFacilities = facilities.filter(f => {
        const matchesBuilding = f.building === activeBuilding;
        const matchesSearch = f.name.includes(searchTerm) ||
            f.description.includes(searchTerm) ||
            f.equipment?.some(e => e.includes(searchTerm));
        return matchesBuilding && matchesSearch;
    });

    const featuredImages = [
        { src: '/assets/auditorium_real_1.jpg', title: '대강당', desc: '최첨단 음향 시설과 대형 스크린' },
        { src: '/assets/medium_conf_real_1.jpg', title: '중회의실', desc: '효율적인 비즈니스 미팅' },
        { src: '/assets/video_conf_real_1.jpg', title: '화상회의실', desc: '글로벌 소통의 중심' },
    ];

    return (
        <Container size="xl" py="xl">
            {/* Header with Carousel */}
            <Box mb={50}>
                <Carousel
                    withIndicators
                    height={400}
                    slideSize="100%"
                    slideGap="md"
                    loop
                    align="start"
                    plugins={[autoplay.current]}
                    onMouseEnter={autoplay.current.stop}
                    onMouseLeave={autoplay.current.reset}
                    style={{ borderRadius: '16px', overflow: 'hidden', boxShadow: '0 8px 30px rgba(0,0,0,0.1)' }}
                    mb="xl"
                >
                    {featuredImages.map((item, index) => (
                        <Carousel.Slide key={index}>
                            <Box pos="relative" h="100%">
                                <Image src={item.src} h="100%" fit="cover" />
                                <Box style={{
                                    position: 'absolute',
                                    bottom: 0,
                                    left: 0,
                                    right: 0,
                                    padding: '40px',
                                    background: 'linear-gradient(to top, rgba(0,0,0,0.8), transparent)',
                                    color: 'white'
                                }}>
                                    <Title order={2} mb="xs">{item.title}</Title>
                                    <Text size="lg" c="gray.2">{item.desc}</Text>
                                </Box>
                            </Box>
                        </Carousel.Slide>
                    ))}
                </Carousel>

                <Box style={{ textAlign: 'center' }}>
                    <motion.div
                        initial={{ opacity: 0, y: -20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.5 }}
                    >
                        <Building2 size={48} color="#007AFF" style={{ marginBottom: '1rem' }} />
                    </motion.div>
                    <Title order={1} mb="sm" style={{ fontFamily: 'var(--font-sf)', fontWeight: 800, fontSize: '3rem' }}>
                        시설 안내
                    </Title>
                    <Text c="dimmed" size="lg" maw={600} mx="auto">
                        NH 방송운영단의 최첨단 시설을 본관과 신관으로 나누어 소개합니다.
                    </Text>
                </Box>
            </Box>

            {/* Controls */}
            <Group justify="center" mb="xl">
                <SegmentedControl
                    value={activeBuilding}
                    onChange={setActiveBuilding}
                    data={buildings.map(b => ({ label: b.name, value: b.id }))}
                    size="lg"
                    radius="xl"
                    color="blue"
                    bg="gray.1"
                />
            </Group>

            <Group justify="flex-end" mb="xl">
                <TextInput
                    placeholder="장소 검색..."
                    leftSection={<Search size={16} />}
                    radius="md"
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.currentTarget.value)}
                    style={{ width: '300px' }}
                />
            </Group>

            {/* Grid */}
            <motion.div layout>
                <SimpleGrid cols={{ base: 1, sm: 2, lg: 3 }} spacing="xl">
                    <AnimatePresence mode="popLayout">
                        {filteredFacilities.map(facility => (
                            <FacilityCard
                                key={facility.id}
                                facility={facility}
                                onClick={setSelectedFacility}
                            />
                        ))}
                    </AnimatePresence>
                </SimpleGrid>
            </motion.div>

            {filteredFacilities.length === 0 && (
                <Box py={50} ta="center">
                    <Text c="dimmed">검색 결과가 없습니다.</Text>
                </Box>
            )}

            {/* Modal */}
            <FacilityModal
                facility={selectedFacility}
                onClose={() => setSelectedFacility(null)}
            />
        </Container>
    );
}

export default function Facilities() {
    return (
        <Suspense fallback={<LoadingOverlay visible={true} />}>
            <FacilitiesContent />
        </Suspense>
    );
}
